


.bigMapContainer{
    height: 90vh;
    width: 93vw;
    margin-top: 2vh; 
}

.markersMapMain{
margin-top: 200vh;
width: 100px;
}

.markersMap{
width: 100%;
height: 100%;
}

.markersMapMain{
    height: 120vh;
    width: 120vw;
    
}

.Overlay{
    background: black;
    z-index: 20
}

@media screen and (min-width:600px){
    .modal-wrapper {
        background: white;
        border: 1px solid #d0cccc;
        box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2), 0 7px 20px 0 rgba(0,0,0,0.17);
       margin-top:40vh;
       margin-left: 20vw;
        transition: all .8s;
        width: 60%;
        height: 40vh;
        position: absolute;
        z-index: 5;
        background-blend-mode: screen;
    }

    .modal-footer {
        background: #263238;
        height: 35px;
        margin-top: 3vh;
    }
}

@media screen and (max-width:600px){
    .modal-wrapper {
        background: white;
        border: 1px solid #d0cccc;
        box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2), 0 7px 20px 0 rgba(0,0,0,0.17);
       margin-top:10vh;
       margin-left: 20vw;
        transition: all .8s;
        width: 60%;
        height: 63vh;
        position: absolute;
        z-index: 5;
        background-blend-mode: screen;
    }

    .modal-footer {
        background: #263238;
        height: 35px;
        margin-top: 25vh;
    }
}




.modal-header {
    background: #263238;
    height: 10vh;
    line-height: 40px;
    text-align: right;
    
}

.modal-header h3 {
    color: white;
    float: left;
    margin: 0;
    padding: 0;
}

.modal-body {
    padding: 5vh;
    text-align: center;
    height: 26vh;

    
}

.modal-image{
   /* border-style: solid;
    border-width: 2px;
    border-color: red;*/
    float: left;
    width: 35%;
    height: 20vh;
}



.close-modal-btn {
    color: white;
    cursor: pointer;
    float: right;
    font-size: 30px;
    margin: 0;
}

.close-modal-btn:hover {
    color: black;
}

.btn-cancel, .btn-continue {
    background: coral;
    border: none;
    color: white;
    cursor: pointer;
    font-weight: bold;
    outline: none;
    padding: 10px;
}

.btn-cancel {
    background-color: #b71c1c;
    float: left;
}

.btn-continue {
    background-color: #1b5e20;
    float: right;
}

.back-drop {
    background-color: rgba(48, 49, 48, 0.42);
    height: 100%;
    position: fixed;
    transition: all 1.3s;
    width: 100%;
}

.open-modal-btn {
    margin: 15px;
    padding: 10px;
    font-weight: bold;
}

.modal-data{
    width:62%;
    height: 25vh;


}

.modal-data-field{
    /*border-style: solid;
    border-width: 2px;
    border-color:green;*/
    margin-top: 1vh;
    text-align: left;
    margin-left: 2vw;
 
}


.modal-data-field p{
font-weight: bold;
display: inline-block;
 
}
@media screen and (min-width:600px){

    .carouselContainer{
        width: 93vw;
        height: 30vh;
        margin-top: 2vh;
        overflow-x: scroll;
        background-color: white;
        border-radius: 10px;
        padding-top: 3vh;
    }
    
    .carouselImage{
    
        max-height: 23vh;
        max-width: 25%;
        float: left;
        margin-left: 2vw;
    }

    .singleMarkerMapContainer{
        width: 93vw;
        height: 30vh;
        background-color: #D8F6F6;
        margin-top: 2vh;
    }

}

@media screen and (max-width:600px){

    .carouselContainer{
        width: 93vw;
        height: 30vh;
        margin-top: 2vh;
        overflow-x: scroll;
    
        background-color: white;
        border-radius: 10px;
        padding-top: 3vh;
    }
    
    .carouselImage{
    
        max-height: 25vh;

        float: left;
        margin-left: 2vw;
    }

    .singleMarkerMapContainer{
        width: 35vh;
        height: 30vh;
        background-color: #D8F6F6;
        margin-top: 2vh;
        display: inline-block;
    }

}
  





